"use client";

"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

type Obat = {
  id: number;
  nama: string;
  jenis: string;
  harga: number;
};

export default function AdminPage() {
  const router = useRouter();
  const [obatList, setObatList] = useState<Obat[]>([]);
  const [nama, setNama] = useState("");
  const [jenis, setJenis] = useState("");
  const [harga, setHarga] = useState("");
  const [editId, setEditId] = useState<number | null>(null);

  useEffect(() => {
    const isLoggedIn = localStorage.getItem("isLoggedIn");
    if (!isLoggedIn) {
      router.push("/login");
    }
  }, [router]);

  const resetForm = () => {
    setNama("");
    setJenis("");
    setHarga("");
    setEditId(null);
  };

  const handleAddOrUpdate = () => {
    if (!nama || !jenis || !harga) return;
    const hargaNum = Number(harga);
    if (isNaN(hargaNum)) return;

    if (editId !== null) {
      setObatList((prev) =>
        prev.map((obat) =>
          obat.id === editId ? { ...obat, nama, jenis, harga: hargaNum } : obat
        )
      );
    } else {
      const newObat: Obat = {
        id: Date.now(),
        nama,
        jenis,
        harga: hargaNum,
      };
      setObatList((prev) => [...prev, newObat]);
    }
    resetForm();
  };

  const handleEdit = (id: number) => {
    const obat = obatList.find((o) => o.id === id);
    if (obat) {
      setNama(obat.nama);
      setJenis(obat.jenis);
      setHarga(obat.harga.toString());
      setEditId(id);
    }
  };

  const handleDelete = (id: number) => {
    setObatList((prev) => prev.filter((o) => o.id !== id));
    if (editId === id) {
      resetForm();
    }
  };

  const jenisOptions = ["ORAL", "CAIRAN", "KAPSUL", "PUYER", "ALKES"];

  const handleLogout = () => {
    localStorage.removeItem("isLoggedIn");
    router.push("/login");
  };

  const [selectedMenu, setSelectedMenu] = useState("inputObat");

  return (
    <div className="min-h-screen bg-black text-white p-8">
      <header className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold">Admin Dashboard - Sistem Apotek</h1>
        <Button onClick={handleLogout} variant="outline" className="text-white border-white">
          Logout
        </Button>
      </header>
      <nav className="mb-8 flex space-x-4 border-b border-gray-600 pb-2">
        <button
          className={`px-4 py-2 rounded ${
            selectedMenu === "inputObat" ? "bg-white text-black" : "hover:bg-gray-700"
          }`}
          onClick={() => setSelectedMenu("inputObat")}
        >
          Input Obat
        </button>
        <button
          className={`px-4 py-2 rounded ${
            selectedMenu === "inputStok" ? "bg-white text-black" : "hover:bg-gray-700"
          }`}
          onClick={() => setSelectedMenu("inputStok")}
        >
          Input Stok Obat
        </button>
        <button
          className={`px-4 py-2 rounded ${
            selectedMenu === "stokKeluar" ? "bg-white text-black" : "hover:bg-gray-700"
          }`}
          onClick={() => setSelectedMenu("stokKeluar")}
        >
          Stok Keluar Obat
        </button>
        <button
          className={`px-4 py-2 rounded ${
            selectedMenu === "riwayatObat" ? "bg-white text-black" : "hover:bg-gray-700"
          }`}
          onClick={() => setSelectedMenu("riwayatObat")}
        >
          Riwayat Obat
        </button>
      </nav>
      <main>
        {selectedMenu === "inputObat" && (
          <>
            <section className="bg-white text-black p-6 rounded-lg shadow-md mb-8">
              <h2 className="text-2xl font-semibold mb-4">{editId !== null ? "Edit Obat" : "Input Nama Obat"}</h2>
              <div className="mb-4">
                <Label htmlFor="nama">Nama Obat</Label>
                <Input
                  id="nama"
                  type="text"
                  value={nama}
                  onChange={(e) => setNama(e.target.value)}
                  required
                />
              </div>
              <div className="mb-4">
                <Label htmlFor="jenis">Jenis Obat</Label>
                <Select onValueChange={(value) => setJenis(value)} value={jenis} defaultValue="">
                  <SelectTrigger id="jenis" className="w-full">
                    <SelectValue placeholder="Pilih jenis obat" />
                  </SelectTrigger>
                  <SelectContent>
                    {jenisOptions.map((option) => (
                      <SelectItem key={option} value={option}>
                        {option}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="mb-4">
                <Label htmlFor="harga">Harga Obat</Label>
                <Input
                  id="harga"
                  type="number"
                  value={harga}
                  onChange={(e) => setHarga(e.target.value)}
                  required
                />
              </div>
              <Button onClick={handleAddOrUpdate} className="mb-4">
                {editId !== null ? "Update Obat" : "Tambah Obat"}
              </Button>
            </section>
            <section className="bg-white text-black p-6 rounded-lg shadow-md">
              <h2 className="text-2xl font-semibold mb-4">Daftar Obat</h2>
              {obatList.length === 0 ? (
                <p>Tidak ada data obat.</p>
              ) : (
                <table className="w-full border-collapse border border-gray-300">
                  <thead>
                    <tr>
                      <th className="border border-gray-300 p-2 text-left">Nama Obat</th>
                      <th className="border border-gray-300 p-2 text-left">Jenis Obat</th>
                      <th className="border border-gray-300 p-2 text-left">Harga</th>
                      <th className="border border-gray-300 p-2 text-left">Aksi</th>
                    </tr>
                  </thead>
                  <tbody>
                    {obatList.map((obat) => (
                      <tr key={obat.id}>
                        <td className="border border-gray-300 p-2">{obat.nama}</td>
                        <td className="border border-gray-300 p-2">{obat.jenis}</td>
                        <td className="border border-gray-300 p-2">Rp {obat.harga.toLocaleString()}</td>
                        <td className="border border-gray-300 p-2 space-x-2">
                          <Button size="sm" variant="outline" onClick={() => handleEdit(obat.id)}>
                            Edit
                          </Button>
                          <Button size="sm" variant="destructive" onClick={() => handleDelete(obat.id)}>
                            Hapus
                          </Button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}
            </section>
          </>
        )}
        {selectedMenu === "inputStok" && (
          <section className="bg-white text-black p-6 rounded-lg shadow-md">
            <h2 className="text-2xl font-semibold mb-4">Input Stok Obat</h2>
            <p>Feature coming soon...</p>
          </section>
        )}
        {selectedMenu === "stokKeluar" && (
          <section className="bg-white text-black p-6 rounded-lg shadow-md">
            <h2 className="text-2xl font-semibold mb-4">Stok Keluar Obat</h2>
            <p>Feature coming soon...</p>
          </section>
        )}
        {selectedMenu === "riwayatObat" && (
          <section className="bg-white text-black p-6 rounded-lg shadow-md">
            <h2 className="text-2xl font-semibold mb-4">Riwayat Obat</h2>
            <p>Feature coming soon...</p>
          </section>
        )}
      </main>
    </div>
  );
}
